.game-btn-rule button {
  box-shadow: rgb(252 148 13 / 60%) 0px 0px 10px;
  background-color: rgb(252, 148, 13);
  border-radius: 18px;
  color: white;
  width: 220px;
  height: 34px;
  margin-top: 10px;

  font-size: 20px;
}
.hd-rule,
.td-rule {
  border: 1px solid black;

  text-align: center;
  height: 44px;
}

.td-row {
  background-color: rgb(241, 243, 255);
  color: gray;
}

.table-rule {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 80px;
}

.hd-rule-violet {
  border: 1px solid black;
  color: rgb(102, 85, 211);
  text-align: center;
  height: 44px;

  font-weight: 600;
}
.td-rule-red {
  border: 1px solid black;
  color: red;
  text-align: center;
  height: 44px;

  font-weight: 600;
}

.td-rule-green {
  border: 1px solid black;
  color: green;
  text-align: center;
  height: 44px;

  font-weight: 600;
}
.got-it-btn{
    background-color: rgb(0, 147, 255);
    color: white;
    z-index: 1;
    position: fixed;
    top: 90%;
    width: 94.8%;
    height: 44px;
    border-radius: 5px;
    font-size: 20px;
}

.no-style{
    background-color: rgb(80, 81, 114);
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 20px;
    color: white;
}