.center-nav {
  display: flex;
  margin-top: 15px;
  align-items: center;
  overflow-x: hidden;
}
.center-nav {
  display: flex;
  margin-top: 15px;
  align-items: center;
  color: black;
}

.fixed {
  position: fixed;
  display: flex;
  top: 0px;
  justify-content: center;
  align-items: center;
}

.allcenter {
  display: flex;
  justify-content: center;
}
.parity {
  background-color: rgb(0, 147, 255);
  color: white;
  padding: 10px;
  padding-left: 35;
  padding-right: 35px;
  padding-left: 35px;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
}
.parity1 {
  color: rgb(0, 147, 255);
  padding: 17px;
  padding-left: 35;
  padding-right: 60px;
  padding-left: 60px;
  font-weight: 600;
  border-right: 1px solid;
  background-color: white;
}
.rangeDice {
  width: 100%;
}

.rangeNumber {
  display: flex;
}

.rangeNumber > p {
  margin: 10px;
  text-align: center;
}

.card {
  flex-direction: row;
  width: 100%;
  text-align: center;
}

.rule-left {
  border-bottom: 1px solid gray;
  padding: 17px;
  background-color: white;
}
.rulediv {
  display: flex;
}

.center-arrow {
  border-bottom: 1px solid gray;
  padding: 15p;
  padding: 12px;
}
.cenperiod {
  display: flex;
  width: 450px;
  justify-content: space-between;
}

.coutdown {
  display: inline-block;
  text-align: center;
  background-color: rgb(241, 243, 255);
  border-bottom: 4px solid #cdd4ff;
  padding: 0 4px;
  color: #383b45;
  font-size: 24px;
  font-weight: 600;
  font-family: sans-serif;
  margin: 0 2px;
  height: 32px;
  width: 26px;
  line-height: 32px;
  border-radius: 4px;
  margin-top: 0px;
  margin-bottom: 0px;
  /* margin-left: 0px; */
}
.countset {
  margin-top: 0px;
  margin-bottom: 0px;
  margin-left: 42px;
  width: 100%;
}

.Cout {
  color: hsl(0, 0%, 59%);
  font-size: 18px;
  margin-left: 30px;
  padding-left: 35px;
  width: auto !important;
  height: auto !important;
  margin-top: 0px;
  margin-bottom: 0px;
}

.per {
  color: #979797;
  font-size: 20px;
  width: auto !important;
  height: auto !important;
  margin-top: 0px;
  margin-bottom: 0px;
}
.pernum {
  font-size: 24px;
  font-weight: 600;
  margin-top: 0px;
  margin-bottom: 0px;
  color: black;
  overflow-x: hidden;
}

.join {
  display: flex;
  align-items: center;
  background-color: rgb(0, 194, 130);
  padding: 15px;
  border-radius: 5px;
  color: white;
  justify-content: center;
  font-weight: 500;
  margin: 2px;
  height: 50px;
  flex-direction: column;
  width: 110px;
}
.joindisable {
  display: flex;
  align-items: center;
  background-color: rgb(194, 180, 180);
  padding: 15px;
  border-radius: 5px;
  color: white;
  justify-content: center;
  font-weight: 500;
  margin: 2px;
  height: 50px;
  flex-direction: column;
  width: 110px;
}

.voilet {
  display: flex;
  align-items: center;
  background-color: rgb(102, 85, 211);
  padding: 20px;
  border-radius: 5px;
  color: white;
  justify-content: center;
  font-weight: 500;
  height: 40px;
  flex-direction: column;
  width: 110px;
}

.voiletdisable {
  display: flex;
  align-items: center;
  background-color: rgb(194, 180, 180);
  padding: 20px;
  border-radius: 5px;
  color: white;
  justify-content: center;
  font-weight: 500;
  height: 40px;
  flex-direction: column;
  width: 110px;
}

.red {
  background-color: rgb(250, 60, 9);
  padding: 20px;
  border-radius: 5px;
  color: white;
  justify-content: center;
  font-weight: 500;
  display: flex;
  align-items: center;
  margin: 2px;
  height: 40px;
  flex-direction: column;
  width: 110px;
}

.reddisble {
  background-color: rgb(194, 180, 180);
  padding: 20px;
  border-radius: 5px;
  color: white;
  justify-content: center;
  font-weight: 500;
  display: flex;
  align-items: center;
  margin: 2px;
  height: 40px;
  flex-direction: column;
  width: 110px;
}

.blow {
  display: flex;
  justify-content: center;
  color: gray;
}

.tablenum {
  width: 80px;
  height: 44px;
  margin: 5px;
}

.scrollbar-indiv {
  height: 700px;
  margin-top: 56px;
}
.disble {
  width: 80px;
  height: 44px;
  margin: 5px;
  opacity: 0.2;
}

.over-btn {
  border: none;
  background: no-repeat;
  display: contents;
}
@media screen and (max-width: 780px) {
  .parity {
    padding-right: 10px;
    padding-left: 10px;
  }
  .pernum {
    font-size: 20px;
  }
  .parity1 {
    padding-right: 30px;
    padding-left: 30px;
  }

  .tablenum {
    width: 61.5px;
    height: 44px;
    margin: 5px;
  }
  .disble {
    width: 61.5px;
    height: 44px;
    margin: 5px;
    opacity: 0.2;
  }
  .mo {
    margin-left: -8px !important;
  }
  .allcenter {
    display: block;
  }

  .cenperiod {
    display: flex;
    width: 355px;
    justify-content: space-between;
  }

  .join {
    padding: 4px;
    width: 105.5px;
    margin: 2px;
    height: 42px;
  }

  .voilet {
    padding: 5px;
    width: 105.5px;

    font-weight: 500;
    height: 40px;
  }

  .red {
    width: 105.5px;
    padding: 8px;

    margin: 2px;
    height: 35px;
  }

  .reddisble {
    width: 105.5px;
    padding: 8px;

    margin: 2px;
    height: 35px;
  }
  .voiletdisable {
    padding: 5px;
    width: 105.5px;

    font-weight: 500;
    height: 40px;
  }

  .joindisable {
    padding: 4px;
    width: 105.5px;
    margin: 2px;
    height: 42px;
  }
}

@media screen and (max-width: 400px) {
  .pernum {
    font-size: 17px;
  }
}
