/*  */

.flex {
  display: flex;
}

.dice-stat {
  justify-content: space-between;

  padding: 4px;
  box-shadow: rgb(0 0 0 / 10%) 0px 0px 10px;
  border-color: rgb(217, 217, 217);
  border-width: 1px;
  border-radius: 10px;
  width: 100%;
  align-items: center;
  padding: 10px 15px;
}

.dice-stat > .box {
  flex: 1 1 0%;

  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 0px;
  padding: 0px;
}

.line {
  width: 1px;

  background-color: skyblue;
  height: 30px;
  box-sizing: border-box;
  display: flex;
  flex-basis: auto;
  flex-direction: column;
  flex-shrink: 0;
}

.bloc-tabs1 {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}

.box > h2 {
  margin: 0px;
  padding: 0px;
  font-weight: normal;
}

.thumb {
  box-shadow: rgb(0 0 0 / 30%) 0px 0px 5px;
  height: 28px;
  width: 28px;
  z-index: 9999999;
  display: flex;
  justify-content: center;
  align-items: center;
  border: white solid;
  border-radius: 14px;
  border-width: 6px;
  background-color: rgb(217, 217, 217);

  outline: none !important;
  box-sizing: border-box;
}

.horizontal-slider {
  width: 100%;
  align-items: center;
  display: flex;
  margin-top: 50px;
}

.track {
  height: 6px;
  border-radius: 3px;

  z-index: 1;
}

.track.track-0 {
  background-color: rgb(250, 60, 9) !important;
  z-index: 1;
}

.track.track-1 {
  background-color: rgb(217, 217, 217);
}

.tooltip {
  background-image: url("../../../images/bubbleNew.png");
  position: absolute;
  background-size: contain;
  color: white;
  z-index: 99999;
  bottom: 10px;
  margin-bottom: 100%;
  width: 30px;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px;
  background-position: 50% 150%;
  border: none !important;
  outline: none !important;
}

.tooltip:focus-visible .thumb:focus-visible {
  outline: none !important;
}

.range {
  justify-content: space-between;
  margin-top: 20px;
  margin-bottom: 10px;
}

.range > h6 {
  font-weight: normal;
  font-size: 13px;
  padding: 0;
  margin: 0;
}

.btn {
  appearance: none;
  background-color: rgb(0, 147, 255);
  font-weight: 700;
  font-size: 18px;
  color: white;
  box-shadow: rgb(0 0 0 / 30%) 0px 0px 5px;
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  cursor: pointer;
}

.btn:disabled {
  background-color: rgb(217, 217, 217);
  cursor: default;
}

.animal-img {
  width: 100px;
  height: auto; 
  border-radius: 10px;
  padding: 10px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.lion {
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.dragon-box {
  margin: auto;
  padding: 15px 0px;
  align-items: center;
  gap: 20px;
}

.flash {
  height: 100px;
  
