.container1 {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 460px;
  height: 300px;
  margin: 30px auto 0;
  word-break: break-all;
  margin-top: 0px;
}

.fasti {
  height: 30px;
}
.center-arrow2 {
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid gray;
  border-right: 1px solid blue;
  padding-right: 30px;
  padding-left: 5px;
}

.rule-left1 {
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid gray;
  border-left: 1px solid blue;
  padding-left: 5px;
}

.ruleleft {
  margin-left: 10px;
}
.bloc-tabs1 {
  display: flex;
  position: fixed;
  background: white;
  width: 460px;
}
.tabs1 {
  height: 38px;
  text-align: center;
  width: 34%;
  background-color: white;
  cursor: pointer;
  box-sizing: content-box;
  position: relative;
  outline: none;
  font-size: 15px;
  font-weight: 600;
  color: rgb(0, 147, 255);
  display: flex;
  justify-content: center;
  align-items: center;
}

.tabs2 {
  height: 38px;
  text-align: center;
  width: 34%;
  background-color: rgb(0, 147, 255);
  cursor: pointer;
  box-sizing: content-box;
  position: relative;
  outline: none;
  font-size: 15px;
  font-weight: 600;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

button {
  border: none;
}
.content-tabs {
  flex-grow: 1;
}
.content {
  display: none;
}
.content h2 {
  padding: 0px 0 5px 0px;
}
.content hr {
  width: 100px;
  height: 2px;
  background: #222;
  margin-bottom: 5px;
}
.content p {
  width: 100%;
  height: 100%;
}
.active-content {
  display: block;
}
.dicegame > h4 {
  margin-top: 20px;
}

@media screen and (max-width: 780px) {
  .bloc-tabs1 {
    display: flex;
    position: fixed;
    background: white;
    width: 372px;
  }
  .dicegame > h4 {
    margin-top: 10px;
  }
  .tabs1 {
    height: 38px;

    width: 29%;
  }
  .dicegame {
    color: black;
  }

  .tabs2 {
    height: 38px;

    width: 29%;
  }
  .mobilewidth {
    color: white;
    background-color: white;
  }
  .rule-left1 {
    color: black;
  }

  .container1 {
    width: 360px !important;
    height: 300px;
    margin: 0px auto 0;
    word-break: break-all;
    margin-top: 0px;
  }
}

@media screen and (max-width: 400px) {
  .rule-left1 {
    /* margin-right: auto; */
    color: black;
  }
  .dicegame {
    color: black;
  }
  .dicegame > h4 {
    margin-top: 10px;
  }
  /* .ruleleft {
    margin-left: auto;
  } */
}
