.comfirm-btns button {
  width: 100%;
  height: 40px;
  border-radius: 10px;
  background: rgb(0, 147, 255);
  color: white;
  font-size: 20px;
  font-weight: 600;
}
.main-choice-div-recharge10 {
  padding: 20px;
  position: relative;
  width: 100%;
}
.div-circle1 {
  width: 70px;
  height: 40px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  box-shadow: 0 0.053333rem 0.533333rem 0.053333rem #9c9090;
}
.div-circle-black1 {
  background-color: black;
  color: white;
  width: 70px;
  height: 40px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.recharge-choice-div-point1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  box-shadow: 0 0.053333rem 0.533333rem 0.053333rem #9c9090;
  border-radius: 5px;
  margin-bottom: 10px;
  margin-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
}
.recharge-choice-div-point1 button {
  background-color: rgb(252, 148, 13);
  height: 30px;
  color: white;
  width: 120px;
  border-radius: 15px;
}

.contract-p {
  color: gray;
  margin-top: 0px;
  margin-bottom: 7px;
}

.add-div1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.add-div1 button {
  background: no-repeat;
  border: 1px solid gray;
  border-radius: 5px;
  width: 50px;
  height: 35px;
}

.red-amount {
  color: red;
  font-size: 22px;
  font-weight: 700;
}

.center-error {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.center-error p {
  margin-top: 0px;
  margin-bottom: 0px;
}
.center-error h2 {
  margin-top: 0px;
  margin-bottom: 0px;
}
.center-error button {
  width: 100%;
  height: 30px;
  border-radius: 5px;
  background: rgb(0, 147, 255);
  color: white;
  margin-top: 20px;
}
.left-deliver-div {
  padding-left: 60%;
}

.left-deliver-div-inear {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.left-deliver-div-inear-p p {
  margin-top: 0px;
  margin-bottom: 0px;
  color: gray;
  font-size: 12px;
}
.left-deliver-div-inear-p {
  margin-left: 20px;
}
